<template>
	<view class="page">
		
    <mescroll-body ref="mescrollRef"
                   @down="downCallback"
                   @up="upCallback"
                   :down="downOption"
                   :up="upOption"
                   :top="0">
		<view class="main">
			<!-- 为你推荐 -->
			<view class="recommend-info">
				<view class="recommend-title">
					<image src="/static/invest_bg.png" mode="widthFix"></image>
					
				</view>
				<view class="inve-box">
					<view class="inve-t1">
						<text class="t1a">我的兑换分</text>
						<text class="t1b">规则</text>
					</view>
					<view class="inve-t2">
						<text class="t2a">21323</text>
						<text class="t2b">点击兑换</text>
					</view>
					<view class="inve-t3">
						兑换分可以兑换商品，避免失效请尽快使用
					</view>
					<view class="inve-t4">
						<view class="t4a">专属兑换分<text class="t4a1">213</text></view>
						<view class="t4b">3天过期<text class="t4b1">213</text></view>
					</view>
				</view>
				<view class="clearheight300"></view>
				<view class="head-bg">
					<view class="session">
						<view class="list" :class="catestatus == v ?'action':''" v-for="(item,v) in cate" @click="changecate(item.id,v)">
							<text class="time">{{item.title}}</text>
							<text class="status">{{item.sort_title}}</text>
						</view>
					</view>
				</view>
				<view class="djsbox">
					兑换倒计时
					<text>{{dtian}}</text>:
					<text>{{dshi}}</text>:
					<text>{{dfen}}</text>:
					<text>{{dmiao}}</text>
				</view>
				<view class="goods-list">
					<view class="list" v-for="(item,index) in goodsList" @click="onSkip('goods')" :key="index">
						<view class="pictrue">
							<image :src="item.imgurl" mode="heightFix"></image>
						</view>
						<view class="title-tag">
							<view class="tag">
								<text>特价</text>
								{{item.title}}
							</view>
						</view>
						<view class="price-info">
							<view class="user-price">
								<text class="jifen">{{item.integral}}积分+</text>
								
								<text class="max">{{item.price}}元</text>
							</view>
							<view class="clearheight10"></view>
							<view class="user-price2">
								<text class="min">兑换热度{{item.sell_num}}</text>
								
								<text class="max">{{item.price}}万+</text>
							</view>
							<view class="del-price">
								<text>￥{{item.old_price}}</text>
							</view>
						</view>
						<view class="button-rem">立即兑换</view>
					</view>
				</view>
			</view>
		</view>
    </mescroll-body>
	<TabBar :tabBarShow="2"></TabBar>
	</view>
</template>

<script>
	import TabBar from '../../components/TabBar/TabBar.vue';
// 引入mescroll-mixins.js
import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
export default {
  mixins: [MescrollMixin], // 使用mixin
  components:{
  	TabBar,
  	},
	data(){
		return{
			catestatus:0,
			mescroll: null, // mescroll实例对象 (此行可删,mixins已默认)
			  // 下拉刷新的配置(可选, 绝大部分情况无需配置)
			downOption: {},
			  // 上拉加载的配置(可选, 绝大部分情况无需配置)
			upOption: {
				use: false
			},
			cate:[],
			goodsList:[],
			pageNo:1,
			cate_id:0,
			//倒计时
			endtime:0,
			dtian:0,
			dshi:0,
			dfen:0,
			dmiao:0,
			timer:null,
		}
	},
	onReady() {
		uni.hideTabBar();
		// #ifdef MP
		uni.setNavigationBarTitle({
			title: '疯抢排行',
		})
		uni.setNavigationBarColor({
			frontColor: '#ffffff',
			backgroundColor: '#fe3b0f',
		})
		// #endif
	},
	onLoad() {
		this.getcate();
		this.endtime = new Date().getTime() + 259200000;
		this.timer = setInterval(() => {
			this.showtime()
		}, 200)
	},
	onShow() {
		this.loadData(1);
	},
	methods:{
		changecate(cate_id,index){
			this.catestatus = index;
			this.cate_id = cate_id;
		},
		getcate() {
			let that = this;
			that.req({
				url: 'getcate',
				data: {},
				Loading: true,
				success: function(res) {
					console.log(res)
					that.cate = res.data;
				}
			})
		},
		loadData(pageNo) {
			let that = this;
			that.req({
				url: 'getrank',
				data: {page:pageNo},
				Loading: true,
				success: function(res) {
					console.log(res)
					if (pageNo == 1) {
					  that.goodsList = res.data;
					} else {
					  that.goodsList = that.goodsList.concat(res.data);
					}
				}
			})
		},
		showtime() {
			let that = this;
			var nowtime = new Date(); //获取当前时间
			var lefttime = that.endtime - nowtime.getTime(), //距离结束时间的毫秒数
			
				leftd = Math.floor(lefttime / (1000 * 60 * 60 * 24)), //计算天数
				lefth = Math.floor(lefttime / (1000 * 60) % 60), //计算小时数
				leftm = Math.floor(lefttime / 1000 % 60), //计算分钟数
				lefts = Math.floor(lefttime % 60); //计算秒数
			
				if(lefttime < 0){
					that.dtian = "00";
					that.dshi = "00";
					that.dfen = "00";
					that.dmiao = "00";
					return;
				}
			if (leftd.toString().length < 2) {
				leftd = '0' + leftd
			}
			if (lefth.toString().length < 2) {
				lefth = '0' + lefth
			}
			if (leftm.toString().length < 2) {
				leftm = '0' + leftm
			}
			if (lefts.toString().length < 2) {
				lefts = '0' + lefts
			}
			//返回倒计时的字符串
			that.dtian = leftd;
			that.dshi = lefth;
			that.dfen = leftm;
			that.dmiao = lefts;
			 
		},
		/*下拉刷新的回调, 有三种处理方式:*/
		downCallback(){
			this.loadData(1);
		  this.mescroll.endSuccess();
		},
		/*上拉加载的回调*/
		upCallback(page) {
			console.log(page)
			this.loadData(page);
		  setTimeout(() =>{
			this.mescroll.endByPage(10, 20);
		  },2000)
		},
	
	}
};
</script>

<style scoped lang="scss">
@import 'index.scss';
</style>
